import React from 'react';
import PropTypes from 'prop-types';
import { useStore } from '../store';

function partial(props) {
    const { data, index, handleClick, selectedIndex, childRefs } = props;
    const {
        action,
        disp: { listMetadata },
    } = data;

    const title = `${listMetadata.peopleFirstName} ${listMetadata.peopleLastName}`;
    return (
        <div
            ref={childRefs}
            role="row"
            id={`${index}`}
            key={`result-people-${index}`}
            aria-selected={selectedIndex === index}
            className="autocomplete-concierge__search-result-item"
            onClick={(e) => handleClick(e, { ...data, title })}
            onKeyDown={(e) => handleClick(e, { ...data, title })}
            tabIndex={-1}
        >
            <div className="details" role="gridcell">
                <a title={title} className="title" href={action} tabIndex={-1}>
                    {title}
                    {listMetadata.peopleDepartment && (
                        <div className="autocomplete-results__metadata">
                            <svg className="svg-icon" aria-hidden="true">
                                <title>Department</title>
                                <use href="#building" />
                            </svg>
                            {listMetadata.peopleDepartment
                                .join(',')
                                .replace(',', ', ')}
                        </div>
                    )}

                    {listMetadata.peoplePhone && (
                        <div className="autocomplete-results__metadata">
                            <svg className="svg-icon" aria-hidden="true">
                                <title>Phone number</title>
                                <use href="#phone" />
                            </svg>
                            <span className="sr-only">Phone number </span>
                            {listMetadata.peoplePhone.join(', ')}
                        </div>
                    )}

                    {listMetadata.peopleEmail && (
                        <a
                            href={`mailto:${listMetadata.peopleEmail}`}
                            className="autocomplete-results__metadata"
                            tabIndex={-1}
                        >
                            <svg className="svg-icon" aria-hidden="true">
                                <title>Email</title>
                                <use href="#email" />
                            </svg>
                            {listMetadata.peopleEmail}
                        </a>
                    )}
                </a>
            </div>
        </div>
    );
}

export default function People(props) {
    const { id, label, className, templateId, childRefs, handleClick } = props;
    const { results, selectedIndex } = useStore();

    if (!results[templateId] || results[templateId].length === 0) {
        return null;
    }

    return (
        <div
            id={`result-row-${id}`}
            className={[`autocomplete-concierge__section`, className].join(' ')}
            role="rowgroup"
        >
            <h2 className="autocomplete-concierge__section-title">{label}</h2>
            {results[templateId] &&
                results[templateId].map((data, index) =>
                    partial({
                        data,
                        index,
                        handleClick,
                        selectedIndex,
                        childRefs,
                    })
                )}
        </div>
    );
}

const { string, number, func } = PropTypes;

People.propTypes = {
    id: string.isRequired,
    label: string,
    className: string,
    templateId: number.isRequired,
    childRefs: func.isRequired,
};

People.defaultProps = {
    label: ``,
    className: ``,
};
